.buttonlookalike{
  border-radius: 8px;
  border-style: double;
  background: #C2B280;
  text-decoration:  none;
  color:            black;
  font-family: 'Birmingham',sans-serif;
  letter-spacing: 2px;
  font-weight: bolder;
}

.buttonlookalike-disabled{
  border-radius: 8px;
  border-style: double;
  background: #C2B280;
  text-decoration:  none;
  color:            black;
  font-family: 'Birmingham',sans-serif;
  letter-spacing: 2px;
  font-weight: bolder;
  filter:grayscale(90%);

}

.bottom{
  position: absolute;
  bottom: 0px;
}

.special-sizing{
  min-width: 200px;
  max-width: 300px;
}

.buttonlookalike:hover{
  background-color:            rgb(155, 142, 103);
}

.buttonlookalike:active{
  background-color:            rgb(155, 103, 103);
}

.image-darken:hover{
  filter: brightness(75%);
}

.image-darken:active{
  filter: brightness(25%)  saturate(50) hue-rotate(30deg)  ;
  
}



.override:link,.override:visited    {
  /* Applies to all unvisited links */
  text-decoration:  none;

  } 

.override:hover,.override:active    {
  /* Applies to links under the pointer */
  text-decoration:  none;
  } 

.add-pointer {
  cursor: pointer;
}


.spooky-text{
  font-family: 'Birmingham',sans-serif;
  letter-spacing: 2px;
}

.wolgasttwo-text{
  font-family: 'Wolgast Two',sans-serif;
  font-weight: bolder;
}

.arno-text{
  font-family: 'Arno Pro Display',serif;
  line-height: 99%;
}

.arno-bold-text {
  font-family: 'Arno Pro Bold',serif;
  line-height: 98%;
}

.small-padding-top{
  padding-top: 2px;
}

.arno-text2{
  font-family: 'Arno Pro Display',serif;
  font-size: 16px;
  line-height: 80%;
}

.line-98 {
  line-height: 98%;
}

.fit-content{
